.mission {
	height: 100vh;
	max-width: 100vw;
	display: flex;
	flex-flow: row nowrap;
	justify-content: center;
	align-items: center;
}

.mission-number {
	max-width: 26rem;
}

.mission-mountains {
	background: url("../img/Mission/mountains.png") no-repeat;
	background-size: cover;
	background-position: center;
	box-shadow: $box-shadow;
	width: 66em;
	height: 63em;
	z-index: 1;
}

.mission-text {
	@extend .text;
	text-align: left;
	padding-top: 4rem;
	margin-left: -15rem;
	flex: 0 1 48rem;
	z-index: 1;
}
