.event {
	height: 100vh;
	position: relative;
	text-align: center;
}

.event-letter {
	position: absolute;
	right: 13%;
	filter: brightness(150%) saturate(80%);
}

.event-number {
	display: inline-block;
	margin: 3rem auto;
}

.event-text {
	@extend .text;
	text-align: left;
	margin: 0 auto;
	display: flex;
	justify-content: center;
}

.item {
	width: 50rem;
}
